.square {
  margin: 10px;
  background-color: white;
  width: 120px;
  height: 120px;
  /*border: 1px solid white;*/
  display: flex; 
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: transform 100ms ease;
}

.square:hover {
  transform: scale(1.05)
}

.active {
  margin: 5px;
  width: 130px;
  height: 130px;
  background-color: #f54287;
  display:flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

