.squares_box {
  display: grid; 
  grid-template-columns: repeat(4, 1fr)
}

.handlers {
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center
}

.start_btn {
  padding: 7px 10px;
  border-radius: 2px;
  border: solid 1px white;
  cursor: pointer;
}

.start_btn:hover {
  background: #2c2c2c;
  color: white;
}

.pulse_box {
  display: flex;
  align-items: center;
  background: white;
  border-radius: 3px; 
  padding: 2px 10px;
  color: #2c2c2c;
}

.pulse_label {
  font-size: 17px;
}

.pulse_handler {
  margin: 0 10px;
}

.instrument_select {
  width: 70px;
  padding: 6px 5px;
  border-radius: 2px;
  border: none;
}
